import React, { useState } from 'react'
import { connect } from 'react-redux'
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from '@material-ui/core'
import { Delete } from '@material-ui/icons'

import ActionButton from '../ActionButton'
import agent from '../../agent'

import { DELETE_USER } from '../../constants/actionTypes'

const mapDispatchToProps = (dispatch) => ({
  onDelete: (user) =>
    dispatch({
      type: DELETE_USER,
      payload: agent.Users.delete(user),
      snackbar: { message: 'User deleted', variant: 'success' },
    }),
})

const DeleteUserAlert = ({ onDelete, user }) => {
  const [open, setOpen] = useState(false)

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  const handleSubmit = () => {
    setOpen(false)
    onDelete(user)
  }

  return (
    <>
      <ActionButton action='Delete user' onClick={handleClickOpen}>
        <Delete />
      </ActionButton>
      <Dialog
        open={open}
        onClick={(ev) => ev.stopPropagation()}
        onClose={handleClose}
        onFocus={(ev) => ev.stopPropagation()}
      >
        <DialogTitle>Delete User</DialogTitle>
        <DialogContent>
          <DialogContentText component='div'>
            Are you sure you want to delete{' '}
            <b>
              {user.firstName} {user.lastName}
            </b>
            ? This action will permanently delete:
            <ul>
              <li>all categories</li>
              <li>all ingredients</li>
              <li>all meals</li>
              <li>all plans</li>
              <li>all recipes</li>
              <li>all settings</li>
              <li>all stores</li>
            </ul>
            associated with this user. This action <b>cannot be undone</b>.
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={handleSubmit}
            color='secondary'
            variant='contained'
            autoFocus
          >
            Delete
          </Button>
          <Button onClick={handleClose} variant='contained'>
            Cancel
          </Button>
        </DialogActions>
      </Dialog>
    </>
  )
}

export default connect(() => ({}), mapDispatchToProps)(DeleteUserAlert)
